<template>
  <div>
    <h2>v-bind</h2>
    <section>
      <div>
        <img
          v-bind:src="imgSrc"
          v-bind:width="logoWidth"
          v-bind:height="logoHeight"
          alt=""
        />
        <img
          v-bind:src="imgSrc2"
          v-bind:width="logoWidth"
          v-bind:height="logoHeight"
          alt=""
        />
      </div>
      <pre>
      v-bind  Vue   HTML   
        . 
        HTML      . 
      v-bind  Vue      
      HTML    .
    </pre
      >
    </section>
    <br />
    <hr />
    <label>
      <input
        type="checkbox"
        v-model="toggled"
        :true-value="trueValue"
        :false-value="falseValue"
      />
      {{ toggled }}
    </label>
    <br />
    <hr />
    <input type="radio" v-model="pick" :value="first" />
    <input type="radio" v-model="pick" :value="second" />
    <span>{{ pick }}</span>
    <br />
    <hr />
    <select v-model="selectedNum">
      <option :value="{ number: 123 }">123</option>
    </select>
    <br />
    <hr />
    <div>
      <label
        ><input type="checkbox" v-model="checked" value="" /></label
      >
      <label
        ><input type="checkbox" v-model="checked" value="" /></label
      >
      <label
        ><input
          type="checkbox"
          v-model="checked"
          value=""
        />/</label
      >
      <p> : {{ checked }}</p>
    </div>
    <br />
    <hr />
    <div>
      <label
        ><input type="radio" v-model="selFruit" :value="fruit1" /></label
      >
      <label
        ><input type="radio" v-model="selFruit" :value="fruit2" /></label
      >
      <label
        ><input type="radio" v-model="selFruit" :value="fruit3" /></label
      >
      <p> : {{ selFruit }}</p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      imgSrc: "https://kr.vuejs.org/images/logo.png",
      imgSrc2: "https://github.com/jmk255/source/blob/main/logo.png?raw=true",
      logoHeight: "50",
      logoWidth: "50",
      toggled: "CoC or inSANe?",
      trueValue: "CoC",
      falseValue: "inSANe",
      pick: "",
      first: "The First",
      second: "The Second",
      selectedNum: 0,
      fruit1: "",
      fruit2: "",
      fruit3: "",
      checked: [],
      selFruit: "",
    };
  },
};
</script>

<style scoped>
img {
  display: block;
}
section {
  display: flex;
}
</style>
