<template>
  <div class="container">
    <div id="justify">
      <h2>V-If Directive</h2>
      <pre>
        <b>v-if </b>:   true       
        <b>v-else </b>: v-if else     
      </pre>
    </div>
    <div class="example1">
      <h1 v-if="bRender">bRender = true > display</h1>
      <h1 v-else>bRender = false > display</h1>
    </div>
    <div class="example2">
      <h1 v-if="type === 'A'">type = A > display</h1>
      <h1 v-else-if="type === 'B'">type = B > display</h1>
      <h1 v-else-if="type === 'C'">type = C > display</h1>
      <h1 v-else>type = not A,B,C > display</h1>
    </div>
    <div class="example3">
      <button v-if="login"></button>
      <button v-else></button>
    </div>
  </div>
  <br>
  <hr>
  <div id="justify2">
      <h2>V-Show Directive</h2>
      <pre>
        <b>v-show </b>:   true      
        <b> </b>  v-if     display  
        <b> </b>     v-show,  v-if   
      </pre>
      <h1 id="theTitle" v-show="showTitle" >!</h1>
    </div>
</template>

<script>
export default {
  data() {
    return {
      bRender: true,
      type: "B",
      login : false,
      showTitle : false,
    };
  },
};
</script>

<style scoped>
pre {
  text-align: left;
}
pre > b {
  display: inline-block;
  width: 80px;
}
</style>
