<template>
  <div>
    <a @click.stop="doStop">  </a>
    <form @submit.prevent="onSubmit">
      <input type="submit" value=" " />
    </form>
    <br>
    <hr>
    <a @click.stop.prevent="doStop">{{clickEl}}</a>
    <form @submit.prevent>   </form>
    <br>
    <hr>
    <div id="strBox" @click.self="doStr">
      <button type="button" @click="changeStr"> </button>
      <p> button  strBox div    </p>
      <p>click.self </p>
    </div>
    <br>
    <hr>
    <pre>
      <h1>addEventListner  </h1>
       @click.capture :         
        @click.once :   1 
         @scroll.passive : preventDefault  

      <h2>exact</h2>
      <button type="button" @click.exact="strBox">   .</button>
    </pre>
  </div>
</template>

<script>
export default {
  data(){
    return{
      clickEl : "",
    }
  },
  methods:{
    doStr(){
      this.clickEl = "self";
    },
    changeStr(){
      this.clickEl = "button";
    }
  }
};
</script>

<style scoped>
  div#strBox{
    padding: 50px;
    background-color: pink;
    border: 2px solid hotpink;
  }
</style>
